@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,700");
html *,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
::selection {
  color: #222;
  background: #f9bf3b;
}
html {
  font-size: 100%;
  height: 100%;
}
body {
  font: 1rem/1.45 'Source Sans Pro', sans-serif;
  font-style: normal;
  height: 100%;
  background: #fbfaf8;
  color: #141414;
}
@media only screen and (min-width: 60em) {
  body {
    font-size: 1.4rem;
  }
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
}
header,
main,
footer {
  display: block;
}
